.react-bootstrap-table .table thead {
  font-size: 12px;
  color: var(--emptyTextField);
}

.react-bootstrap-table .table tbody {
  font-size: 14px;
  color: var(--text);
  height: 500px;
}

.react-bootstrap-table .table.example tbody {
  font-size: 14px;
  color: var(--text);
  height: 200px;
}

table.table-width-200 {
  width: 200% !important;
}

.react-bootstrap-table tbody {
  display: block;
  overflow-y: scroll;
}

.react-bootstrap-table thead {
  display: table;
  width: 98.7%;
  table-layout: fixed;
}
.react-bootstrap-table tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}
.SecondColumn {
  position: -webkit-sticky; /* for Safari */
  position: sticky;
  left: 100px; /* This is the width of first column, so second will stick to the right edge of the first column */
  z-index: 999;
}

.table-responsive.card {
  margin-bottom: 0px !important;
}

.react-bootstrap-table-editing-cell .alert {
  margin-top: 10px;
  margin-bottom: 0px;
  background-color: #feefd0;
  border-color: #fde9bd;
  color: #815c15;
  font-weight: normal !important;
}
.react-bootstrap-table-editing-cell .shake {
  animation-name: none !important;
  animation-duration: 0s !important;
}

