/* Tooltip container */
.tooltip-custom {
  position: relative;
  display: block;
}

/* Tooltip text */
.tooltip-custom .tooltiptext {
  visibility: hidden;
  min-width: 200px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px;
  border-radius: 6px;
  margin-left: 10px;
  margin-top: -5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip-custom:hover .tooltiptext {
  visibility: visible;
}

.tooltip-custom .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}

.hiddenTooltip.tooltip-custom:hover .tooltiptext {
  visibility: hidden;
}

.table.reviewTable tbody {
  overflow-x: hidden !important;
}
